@charset "UTF-8";
/* リキッドレイアウト対応 */
:root {
  --base-font-family: "Zen Old Mincho", serif;
  --second-font-family: "Cormorant Garamond", serif;
  --padding-pc: 25px;
  --padding-sp: 20px;
  --regular: 400;
  --medium: 500;
  --bold: 700;
  --black: #000;
  --white: #fff;
}

body {
  font-family: var(--base-font-family);
}

@media only screen and (min-width: 768px) {
  .sp {
    display: none !important;
  }
}
@media only screen and (max-width: 767px) {
  .pc {
    display: none !important;
  }
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
html {
  font-size: 16px;
}
@media (min-width: 768px) {
  html {
    font-size: 0.8333333333vw;
  }
}

/* pcの電話番号発信対応 */
a[href^="tel:"] {
  pointer-events: none;
}
@media screen and (max-width: 767px) {
  a[href^="tel:"] {
    pointer-events: all;
  }
}

a {
  text-decoration: none;
  color: inherit;
}

img {
  height: 100%;
  object-fit: cover;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
  -webkit-text-size-adjust: 100%;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

html {
  overflow-wrap: break-word;
}

.header {
  position: absolute;
  top: 2.6875rem;
  left: 0;
  right: 0;
  width: 100%;
  z-index: 10;
}

.header__nav-list {
  display: flex;
  gap: 2.5rem;
  align-items: center;
  justify-content: center;
}

.header__nav-item {
  font-size: 1.125rem;
}

.header__nav-link {
  font-size: 1.125rem;
  font-weight: var(--regular);
  letter-spacing: 0.12em;
  color: #25201b;
  text-decoration: none;
  transition: opacity 0.3s ease;
}

.inner {
  max-width: 120rem;
  padding: 0 2.8125rem;
  margin-inline: auto;
}

main {
  overflow: hidden;
  opacity: 0;
}

.mv {
  position: relative;
  z-index: 2;
  padding: 2.5rem 2.5rem 0;
  aspect-ratio: 1920/867;
}

.mv__bg {
  position: fixed;
  inset: 0;
  z-index: 8;
  background: linear-gradient(45deg, #fff 0%, #ddcdce 100%);
  mask-image: linear-gradient(to right, transparent 0, #000 50% 100%);
  mask-repeat: no-repeat;
  mask-size: 200% 100%;
  mask-position: 100% 0%;
  pointer-events: none;
}

.mv__logo {
  aspect-ratio: 393/132;
  width: 24.5625rem;
  margin-left: -1.75rem;
  margin-top: -0.8125rem;
}

.mv__logo img {
  object-fit: contain;
}

.mv__flower1 {
  aspect-ratio: 553/788;
  position: absolute;
  z-index: 11;
  width: 28%;
  left: 0;
  bottom: 0;
  translate: 0 45%;
}

.mv__dec {
  opacity: 0;
  aspect-ratio: 657/542;
  width: 34%;
  left: 0;
  position: absolute;
  top: 5.3125rem;
  mix-blend-mode: screen;
}

.mv__images {
  margin-top: -7.5rem;
  display: grid;
  grid-template-columns: 66% 1fr;
  place-content: center;
  gap: 2.375rem;
  position: relative;
  z-index: 1;
}

.mv__img01-wrap {
  z-index: 3;
  grid-row: 1/3;
  position: relative;
  clip-path: inset(-100% -100% 0% -10 0%);
}

.mv__img01 {
  height: 100%;
  overflow: hidden;
  border-radius: 0 2.5rem 2.5rem 2.5rem;
}

.mv__visual {
  aspect-ratio: 710/953;
  position: absolute;
  bottom: 0;
  right: -3.4375rem;
  width: 33vw;
}

.mv__img02-wrap {
  position: relative;
}

.mv__img02 {
  aspect-ratio: 587/307;
  border-radius: 2.5rem;
  overflow: hidden;
}

.mv__img03-wrap {
  position: relative;
}

.mv__img03 {
  aspect-ratio: 587/480;
  border-radius: 2.5rem;
  overflow: hidden;
}

.mv__badge-label {
  width: 18.125rem;
  padding: 0.6875rem 2.3125rem 0.9375rem;
  border-radius: 50%;
  border: 1px solid #fff;
  background: radial-gradient(transparent, rgba(255, 255, 255, 0.4));
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: var(--second-font-family);
  font-size: 3.5rem;
  font-weight: var(--regular);
  line-height: 1.2;
  letter-spacing: 0.08em;
  color: var(--white);
  text-shadow: 0 0 1.25rem #ff5e3e;
}

.mv__slogan {
  position: absolute;
  bottom: 0;
  z-index: 15;
  left: 50%;
  transform: translateX(-50%);
}

.mv__slogan-text {
  display: block;
  font-size: 10.625rem;
  font-weight: 300;
  text-align: center;
  opacity: 0.7;
  line-height: 1.1;
  width: fit-content;
  margin-inline: auto;
  color: transparent;
  background: linear-gradient(45deg, #f4727b 0 10%, #cfc9c9, #f4727b 90% 100%);
  background-size: 100% 100%;
  background-repeat: repeat-x;
  font-family: var(--second-font-family);
  background-clip: text;
  mask-image: linear-gradient(to right, #000 0 50%, transparent 100%);
  mask-repeat: no-repeat;
  mask-size: 200% 100%;
  mask-position: 0% 0%;
}

.mv__vertical-copy {
  position: absolute;
  z-index: 5;
  left: 15.1875rem;
  top: 50%;
  transform: translateY(-50%);
  writing-mode: vertical-rl;
}

.mv__vertical-copy-text {
  display: block;
  letter-spacing: 0.2em;
  line-height: 1.5;
  color: var(--white);
  font-size: 1.875rem;
  font-weight: var(--regular);
  font-family: var(--base-font-family);
  font-feature-settings: "vert";
}

.top-intro {
  position: relative;
  padding: 7.5rem 0 10rem;
  z-index: 1;
}
.top-intro::after {
  content: "";
  display: block;
  position: absolute;
  height: calc(100% + 21.875rem);
  bottom: 0;
  width: 100%;
  left: 0;
  background: linear-gradient(to bottom, #fff 0%, #dca0a2 65% 100%);
  z-index: 1;
}

.top-intro__inner {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 4rem;
  text-align: center;
  position: relative;
  z-index: 4;
}

.top-intro__title {
  margin: 0;
  font-size: 2rem;
  letter-spacing: 0.12em;
  line-height: 1.4;
  color: #25201b;
}

.top-intro__description {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  max-width: 45rem;
}

.top-intro__description-text {
  margin: 0;
  font-size: 1.125rem;
  line-height: 2.1;
  letter-spacing: 0.06em;
  color: #25201b;
}

.top-intro__gallery {
  gap: 2rem;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}

.top-intro__card {
  margin: 0;
  aspect-ratio: 320/360;
  border-radius: 2.5rem;
  overflow: hidden;
}

.top-intro__card-image {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.top-intro__dec {
  position: absolute;
  top: -3.125rem;
  right: -1.875rem;
  width: 37.5rem;
  aspect-ratio: 1;
  z-index: 2;
}

.top-intro__dec img {
  object-fit: contain;
}